<template>
	<view class="cencto">
		<view class="clos" @click="clos">
			关闭
		</view>
		<view class="appmasg">
			<image src="../../static/logo.png" mode="" class="applog"></image>
			<text>36氪 申请使用</text>
		</view>
		<view style="width: 80%; margin-left: 30upx; margin-top: 30upx;">
			<text class="masg" style="font-size: 40upx; font-weight: bold;">你的微信头像、昵称、地区和性别信息</text>
		</view>
		<view class="usermasg">
			<text class="tatle">你可以选择使用不同的个人信息登录</text>
			<view class="user">
				<image src="../../static/logo.png" mode="" style="width: 80upx; height: 80upx;"></image>
				<view class="nicheng">
					<text class="id">天青色</text>
					<text class="zhushi">微信个人信息</text>
				</view>
			</view>
			<view class="user">
				<view class="adduser">
					<text class="add">+</text>
					<text>新建用户信息</text>
				</view>
				<text class="goadd">></text>
			</view>
		</view>
		<view class="but">
			<button type="primary" size="default" open-type="getUserInfo" @getuserinfo="loginByWeixin">同意</button>
			<button type="primary" plain="true" class="but2">拒绝</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
			};
		},
		methods:{
			clos() {
				uni.navigateBack({
					url:'logn'
				})
			},
			
			// unicloud微信登录
			getWeixinCode() {
				return new Promise((resolve, reject) => {
					uni.login({
						provider: 'weixin',
						success(res) {
							console.log('----------------')
							console.log(res);
							resolve(res.code)
						},
						fail(err) {
							console.log('----------------')
							console.log(err);
							reject(new Error('微信登录失败'))
						}
					})
				})
			},
			loginByWeixin() {
				this.getWeixinCode().then((code) => {
					return uniCloud.callFunction({
						name: 'user-center',
						data: {
							action: 'loginByWeixin',
							params: {
								code,
							}
						}
					})
				}).then((res) => {
					uni.showModal({
						showCancel: false,
						content: JSON.stringify(res.result)
					})
					if (res.result.code === 0) {
						uni.setStorageSync('uni_id_token', res.result.token)
						uni.setStorageSync('uni_id_token_expired', res.result.tokenExpired)
					}
				}).catch((e) => {
					console.error(e)
					uni.showModal({
						showCancel: false,
						content: '微信登录失败，请稍后再试'
					})
				})
			},
		}
	}
</script>

<style lang="scss">
	.cencto {
		
		.clos {
			margin-left: 30upx;
			margin-top: 50upx;
		}
		
		.appmasg {
			display: flex;
			align-items: center;
			margin-left: 30upx;
			margin-top: 100upx;
			.applog {
				width: 50upx;
				height: 50upx;
				margin-right: 20upx;
			}
		}
		
		.usermasg {
			width: 80%;
			margin-left: 30upx;
			margin-top: 100upx;
			
			.tatle {
				font-size: 25upx;
				color: #999999;
			}
			
			.user {
				height: 100upx;
				margin-top: 10upx;
				padding-top: 10upx;
				border-top: 1upx solid #999999;
				border-bottom: 1upx solid #999999;
				display: flex;
				
				.nicheng {
					display: flex;
					flex-direction: column;
					margin-left: 20upx;
					
					.zhushi {
						font-size: 20upx;
						color: #999999;
						margin-top: 10upx;
					}
				}
			}
			
			.user:last-child{
				margin-top: 0upx;
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding-top: 0upx;
				
				.adduser {
					margin-left: 30upx;
					
					.add {
						font-size: 55upx;
						color: #999999;
						margin-right: 30upx;
					}
				}
				
				.goadd {
					font-size: 55upx;
					color: #999999;
				}
			}
		}
		
		.but {
			width: 300upx;
			height: 300upx;
			margin-left: 200upx;
			margin-top: 250upx;
			
			.but1 {
				
				background-color: #57be6a;
				color: #fff;
				border: 1upx solid #707070;
				margin-bottom: 30upx;
			}
			
			.btn2 {
				
				background-color: #f2f2f2;
				color: #57be6a;
				border: 1upx solid #707070;
			}
		}
	}
</style>
